import { useState } from 'react';

import React from 'react';
import { Chrono } from 'react-chrono';
import './Style/SalesOrderPage.css';
import TimelineForm from './TimelineForm'; // 1. Import 


const initialItems = [
  {
    title: '',
    cardTitle: '',
    cardSubtitle: ' A  Widget Pro  10 ',
    cardDetailedText: ' #SO-00123',
  },
  {
    title: '/',
    cardTitle: '',
    cardSubtitle: '',
    cardDetailedText: ': ',
  },
  {
    title: 'CR ',
    cardTitle: '',
    cardSubtitle: '',
    cardDetailedText: ': ',
  }, {
    title: 'CR Mgr. /',
    cardTitle: '',
    cardSubtitle: '',
    cardDetailedText: ': ',
  },
  {
    title: 'PL ',
    cardTitle: '',
    cardSubtitle: '',
    cardDetailedText: ': ',
  }, {
    title: 'PL Mgr. /',
    cardTitle: '',
    cardSubtitle: '',
    cardDetailedText: ': ',
  },
  {
    title: 'SV ',
    cardTitle: '',
    cardSubtitle: '',
    cardDetailedText: ': ',
  }, {
    title: 'SV Mgr. /',
    cardTitle: '',
    cardSubtitle: '',
    cardDetailedText: ': ',
  },
  {
    title: '',
    cardTitle: '',
    cardSubtitle: '',
    cardDetailedText: ': ',
  },
];

function SalesOrderPage() {
  

  // 2.  State 
  const [items, setItems] = useState(initialItems);

  // 1.  State  Index  Active
  //    
  const [activeIndex, setActiveIndex] = useState(initialItems.length - 1);

  // 3.   State
  const handleAddItem = (newItem) => {
    //  object  timeline
    const itemToAdd = {
      title: new Date().toLocaleDateString('th-TH'), // 
      ...newItem, //  (cardTitle, cardSubtitle) 
    };
    //  state  array  (...items) 
    setItems([...items, itemToAdd]);
    // 2.   activeIndex
    //     Index  Array 
    setActiveIndex(items.length - 1);
  };

  return (
    //  Content  
    <main className="app-main">
      <div className="app-content-header">
        <h1 className="fs-3"></h1>
      </div>
      <div className="app-content">
        <div className="container-fluid">
          {/*  */}
          {/* 4.   handleAddItem  */}


          <hr className="my-4" />

          {/* 5.  Timeline  State */}
          <div className="custom-timeline">
            {items.length > 0 ? (
              <Chrono
                items={items}
                mode="HORIZONTAL" // 
                cardHeight={150}
                 itemWidth={130}
                disableToolbar={true}
                // 3.  State  activeIndex  Chrono
                //    
                activeItemIndex={activeIndex}
                theme={{
                  
                  primary: '#0d6efd',
                  secondary: '#6c757d',
                  cardBgColor: '#ffffff',
                  cardForeColor: '#333333',
                  titleColor: '#0d6efd',
                }}
              />
            ) : (
              <p></p>
            )}
          </div>
          <TimelineForm onSubmit={handleAddItem} />
        </div>
      </div>
    </main>
  );




}

export default SalesOrderPage;