import React, { useState } from 'react';

//  props  onSubmit ( handleAddItem )
function TimelineForm({ onSubmit }) {
  //  state  
  const [cardTitle, setCardTitle] = useState('');
  const [cardSubtitle, setCardSubtitle] = useState('');

  const handleSubmit = (event) => {
    event.preventDefault(); // 

    // 
    if (!cardTitle || !cardSubtitle) {
      alert('');
      return;
    }

    //  onSubmit  
    onSubmit({ cardTitle, cardSubtitle });

    // 
    setCardTitle('');
    setCardSubtitle('');
  };

  return (
    <div className="card shadow-sm">
      <div className="card-header">
        <h5 className="card-title mb-0"></h5>
      </div>
      <div className="card-body">
        <form onSubmit={handleSubmit}>
          <div className="mb-3">
            <label htmlFor="cardTitle" className="form-label"> (Card Title)</label>
            <input
              type="text"
              className="form-control"
              id="cardTitle"
              value={cardTitle}
              onChange={(e) => setCardTitle(e.target.value)}
              placeholder=" , "
            />
          </div>
          <div className="mb-3">
            <label htmlFor="cardSubtitle" className="form-label"> (Card Subtitle)</label>
            <textarea
              className="form-control"
              id="cardSubtitle"
              rows="2"
              value={cardSubtitle}
              onChange={(e) => setCardSubtitle(e.target.value)}
              placeholder=""
            ></textarea>
          </div>
          <button type="submit" className="btn btn-primary">
            <i className="bi bi-plus-circle-fill me-2"></i>
            
          </button>
        </form>
      </div>
    </div>
  );
}

export default TimelineForm;